import {Redirect} from 'react-router-dom';
import React, { ChangeEvent, FormEvent, FormEventHandler } from 'react';
import './App.css';
import {Landing} from './Landing';

var shouldRedirect = true;

interface redirectProps {
}
interface redirectState {
  redirect: boolean;
}
export class RedirectToEula extends React.Component<redirectProps, redirectState> {
  constructor(props:redirectProps) {
    super(props)
    this.state = {redirect: shouldRedirect};
  }
  renderRedirect = () => {
    if (this.state.redirect) {
      return <Redirect to='/eula' />
    }
  }
  
  render() {
    return(
      <div>
        {this.renderRedirect()}
      </div>
    );
  }
}

interface doneWithEulaState {
    done: any;
}
